import {GameRecord} from "@web3game/typings/openBox";
import { OpenBoxAPI } from "./OpenBoxAPI";
import {openBoxContract, betEvent, loadGame} from "../contractHelps/openBoxContract";
import { getAccounts } from "../helpers/web3Helpers";
import { getWeb3 } from "../helpers/getWeb3";
import { compareByTime } from "../../common/utils/functions/sort";

export class OpenBoxAPIReal implements OpenBoxAPI {

    public async getBetAddress(): Promise<string> {
        const contract = await openBoxContract();
        return contract.address;
    }

    public async getHistoryList(): Promise<object[]> {
        const logs = [];
        const accounts = await getAccounts();
        const events = await betEvent(accounts[0]);
        const web3 = await getWeb3();
        for (let i = 0; i < events.length; i++) {
            const event = events[i];
            logs.push({
                id: event.args.issueNo.toNumber(),
                issueNo: event.args.issueNo.toNumber(),
                money: web3.fromWei(event.args.value.toNumber(), 'ether'),
                time: event.args.betTime.toNumber() * 1000,
                currentUserId: accounts[0],
            });
        }
        return logs.sort(compareByTime());
    }

    public async loadGame(historyData: any): Promise<GameRecord> {
        const game = await loadGame(historyData.issueNo);

        const scores = [];
        const sequencs = [];
        for (let i = 0; i < game[1].length; i++) {
            scores[i] = game[1][i].toNumber();
            sequencs[i] = game[2][i].toNumber();
        }

        const gameRecord: GameRecord = {
            id: historyData.id,
            currentUserId: historyData.currentUserId,
            playerAddrs: game[3],
            playerScores: game[4],
            playerAwards: game[5],
            scores,
            sequencs,
            play: game[3][3] !== '0x0000000000000000000000000000000000000000',
        };

        return gameRecord;
    }
}

export default new OpenBoxAPIReal();
