import Web3 from 'web3'
import {getLang} from "@web3game/lang";

let web3Instance = null;
// const thunderTestNetwork = "18";
// const thunderMainNetwork = "108";

export const getWeb3 = async () => {
  if (web3Instance) {  
    return web3Instance;
  }
  const context = (typeof window === 'object') ? window : global;
  var web3Provider;

  if (context.ethereum) {
    web3Provider = context.ethereum;

    try {
      // 请求用户账号授权
      await context.ethereum.enable();
    } catch (error) {
      // 用户拒绝了访问
      throw new Error("User denied account access.");
    }
  }
  // 老版 MetaMask
  else if (typeof context.web3 !== 'undefined') {
    web3Provider = context.web3.currentProvider;
    console.log('Injected web3 detected.');
  } else {
    // Fallback to localhost if no web3 injection. We've configured this to
    // use the development console's port by default.
    web3Provider = new Web3.providers.HttpProvider('http://127.0.0.1:8545');
    // throw new Error('No web3 instance injected, using Local web3.');
    throw new Error(getLang().WEB3.NO_WEB3_PROVIDER);
  }
  // throw new Error(web3Provider.isTrust);
  // // const networkVersion = web3Provider.networkVersion;
  // // if(networkVersion !== thunderTestNetwork && networkVersion !== thunderMainNetwork) {
  // //   throw new Error(getLang().WEB3.WRONG_NETWORK);
  // // }

  web3 = new Web3(web3Provider);

  web3Instance = web3;

  return web3;
};
