import { promisify } from 'util';
import { getWeb3 } from "./getWeb3";

export async function getAccounts() {
    const { eth } = await getWeb3();
    const getAccountsAsync = promisify(eth.getAccounts);
    return getAccountsAsync();
}

export async function getBalance() {
    const { eth } = await getWeb3();
    const accounts = await getAccounts();
    const getBalanceAsync = promisify(eth.getBalance);
    return getBalanceAsync(accounts[0]);
}

export async function getProviderAndAccounts() {
    const { currentProvider } = await getWeb3();
    const accounts = await getAccounts();
    return {currentProvider, accounts};
}

export async function estimateGas(transactionObject) {
    const { eth } = await getWeb3();
    const estimateGasAsync = promisify(eth.estimateGas);
    return estimateGasAsync(transactionObject);
}

export async function sendTransaction(transactionObject) {
    const { eth } = await getWeb3();
    const sendTransactionAsync = promisify(eth.sendTransaction);
    return sendTransactionAsync(transactionObject);
}